.toggle-menu:focus { outline: none; }

.sandwich { width: 32px; position: absolute; left: 0; right: 0; margin: auto; z-index: 200; top: 50%; transform: translateY(-50%); }

.sw-topper { display: block; position: relative; transform-origin: left top; width: 32px; height: 3px; background: #fff; border: none; transition: transform 0.5s, background-color .5s; margin-bottom: 5px; }

@media only screen and (max-width: 599px) { .sw-topper { margin-bottom: 4px; } }

