@media only screen and (max-width: 1023px) { .contacts__title { text-align: center; } }

.contacts__list { margin-top: 56px; }

@media only screen and (max-width: 1023px) { .contacts__list { max-width: 250px; margin: 56px auto 0; } }

@media only screen and (max-width: 767px) { .contacts__list { margin-top: 20px; } }

.contacts__item:not(:first-child) { margin-top: 32px; }

@media only screen and (max-width: 767px) { .contacts__item:not(:first-child) { margin-top: 10px; } }

.contacts__feedback { max-width: 546px; width: 52%; position: absolute; top: -22px; right: 0; z-index: 5; }

@media only screen and (max-width: 1023px) { .contacts__feedback { position: initial; max-width: none; width: 100%; margin-top: 40px; } }

.contacts-item { position: relative; padding-left: 46px; }

.contacts-item__icon { position: absolute; top: 50%; left: 0; transform: translateY(-50%); }

.contacts-item__icon_phone { font-size: 20px; }

.contacts-item__icon_email { font-size: 15px; }

.contacts-item__icon_telegram { font-size: 22px; }

.contacts-item__icon .icon { display: block; }

.contacts-item__link { position: relative; display: inline-block; overflow: hidden; }

.no-touch .contacts-item__link:hover:before { animation: lineOn 0.3s ease alternate forwards; }

.contacts-item__link:before { position: absolute; content: ""; left: 0; width: 100%; bottom: 3px; height: 1px; background-color: #fff; animation: lineOut 0.3s ease alternate forwards; }

@keyframes lineOn { 0% { transform: translateX(-105%); }
  1% { transform: translateX(-105%); }
  100% { transform: translateX(0); } }

@keyframes lineOut { 0% { transform: translateX(0); }
  100% { transform: translateX(105%); } }

.contacts-icons { position: absolute; left: 0; top: 0; right: 0; bottom: 0; overflow: hidden; }

.contacts-icons__inner { position: relative; height: 100%; }

.contacts-icons__svg { left: 205px; bottom: 25px; z-index: 2; }

.contacts-icons__svg_blurred { filter: blur(10.2px); left: 115px; bottom: -60px; font-size: 200px; z-index: 1; }

.contacts-icons__svg_small { filter: blur(10.2px); font-size: 165px; right: 99px; bottom: auto; top: -35px; left: auto; }

.feedback { padding: 51px 73px 55px; background-color: #fff; border-radius: 8px; box-shadow: 0 0 20px 0 rgba(29, 17, 86, 0.12); }

@media only screen and (max-width: 599px) { .feedback { padding: 20px; } }

.feedback__title { text-align: center; line-height: 1.35; }

.feedback__form { margin-top: 40px; }

@media only screen and (max-width: 599px) { .feedback__form { margin-top: 20px; } }

.feedback-form { position: relative; }

.feedback-form__item:not(:first-child) { margin-top: 25px; }

.feedback-form__submit { min-height: 50px; max-width: 300px; position: relative; display: flex; justify-content: center; align-items: center; margin: 30px auto 0; }

@media only screen and (max-width: 767px) { .feedback-form__submit { min-height: 80px; } }

.feedback-form__progress-bar { pointer-events: none; position: absolute; height: 10px; width: 0; top: 50%; left: 50%; transform: translateX(-50%) translateY(-50%); border-radius: 200px; background: #0074e4; }

.feedback-form__checkmark { display: flex; justify-content: center; align-items: center; position: absolute; top: 50%; transform: translateY(-50%) translateX(-50%); left: 50%; right: 0; }

.feedback-form__checkmark .icon { font-size: 40px; fill: transparent; stroke: #fff; stroke-width: 2px; stroke-dasharray: 42px; stroke-dashoffset: 42px; stroke-linecap: round; stroke-linejoin: round; }

.form-input { width: 100%; }

.input { display: block; width: 100%; height: 50px; padding: 15px 20px; border-radius: 4px; border: 1px solid rgba(13, 50, 93, 0.2); color: #89969f; font-family: "Roboto", Helvetica, Arial, sans-serif; font-size: 14px; }

.input::placeholder { color: #89969f; font-size: 14px; }

.input.error { color: red; border-color: red; }

.input.error::placeholder { color: red; }

textarea.input { height: 157px; resize: none; }

label.error { position: absolute; top: 100%; left: 0; padding-left: 20px; color: red; font-size: 12px; }

.message-sending { display: none; position: absolute; z-index: 10; top: 0; left: 0; right: 0; bottom: 80px; padding-bottom: 80px; background-color: #fff; user-select: none; color: #0074e4; text-align: center; font-size: 0; transform: translate3d(0, 0, 0); overflow: auto; transition: opacity 0.3s, visibility 0.3s, z-index 0.3s; }

.message-sending:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.message-sending__container { position: relative; z-index: 1; font-size: 24px; font-weight: 300; vertical-align: middle; display: inline-block; max-width: 430px; width: 100%; }

@media only screen and (max-width: 767px) { .message-sending__container { font-size: 22px; } }

@media only screen and (max-width: 599px) { .message-sending__container { font-size: 18px; } }

.message-sending__inner { max-width: 100%; background-color: #fff; }

.message-sending__x { position: absolute; top: 20px; right: 40px; background-color: transparent; font-size: 17px; }

.footer { padding: 80px 0 32px; }

@media only screen and (max-width: 1023px) { .footer { padding: 40px 0; } }

@media only screen and (max-width: 767px) { .footer { padding: 20px 0; } }

.footer__inner { display: flex; justify-content: space-between; align-items: center; }

@media only screen and (max-width: 599px) { .footer__inner { flex-direction: column-reverse; } }

@media only screen and (max-width: 599px) { .footer__copyright { margin-top: 20px; } }

.social { display: flex; }

.social__item:not(:first-child) { margin-left: 30px; }

.social__link { display: flex; align-items: center; justify-content: center; color: #0074e4; width: 56px; height: 56px; border-radius: 50%; border: 1px solid rgba(0, 116, 228, 0.2); font-size: 18px; transition: opacity .3s; }

.no-touch .social__link:hover { opacity: 0.8; }

.social__link_facebook { font-size: 20px; }

.social__link_twitter { font-size: 16px; }

.social__link_telegram { font-size: 17px; }
