.portfolio {
	padding-top: 124px;
	@include sm {
		padding-top: 30px; }
	&__title {
		margin-bottom: 45px;
		@include sm {
			margin-top: 20px; } }
	&__top {
		margin: 40px 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include sm {
			flex-direction: column;
			margin: 10px 0; } }
	&__controls {
		font-size: 14px;
		text-transform: uppercase;
		display: flex;
		@include sm {
			flex-wrap: wrap; }
		@include xs {
			display: none; } }
	&__control {
		position: relative;
		cursor: pointer;
		user-select: none;
		&:not(:first-child) {
			margin-left: 30px; }
		&:before {
			content: "";
			position: absolute;
			left: 50%;
			width: 115%;
			top: 50%;
			transform: translate(-50%, -50%);
			height: 1px;
			opacity: 0;
			visibility: hidden;
			background-color: $blue;
			transition: opacity .3s, visibility .3s; }
		&.is-active {
			&:before {
				opacity: 1;
				visibility: visible; } } }

	&__list {
		display: flex;
		flex-wrap: wrap;
		&-wrap {
			margin: 0 -12px; } }

	// &__item
 }	//     margin: 0 12px

.portfolio-item {
	width: 50%;
	padding: 12px;
	@include sm {
		width: 100%; }
	&__wrap {
		position: relative;
		height: 100%;
		transition: box-shadow .3s;
		box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
		display: block;
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			z-index: 1;
			background-image: linear-gradient(to left, rgba(#000, 0), rgba(#000, 0.05) 53%, rgba(#000, 0.35)); }
		@include hover {
			box-shadow: 0 11px 43px 0 rgba(7, 42, 68, 0.3);
			.view-button {
				.icon {
					transform: translateX(5px); } }
			.portfolio-item__img img {
				transform: scale(1.04); } } }
	&__title,
	&__link {
		position: absolute;
		z-index: 2;
		color: #FFF; }
	&__img {
		// padding: 10px
		height: 100%;
		overflow: hidden;
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			transition: transform .3s;
			@include sm {
				width: 100%; } } }
	&__title {
		font-size: 24px;
		// color: $blue
		font-weight: 400;
		left: 32px;
		top: 35px;
		max-width: 250px; }
	&__link {
		left: 32px;
		bottom: 0; } }
