.feedback {
	padding: 51px 73px 55px;
	background-color: $white;
	border-radius: 8px;
	box-shadow: 0 0 20px 0 rgba($shadowColor, 0.12);
	@include xs {
		padding: 20px; }

	&__title {
		text-align: center;
		line-height: 1.35; }

	&__form {
		margin-top: 40px;
		@include xs {
			margin-top: 20px; } } }


.feedback-form {
	position: relative;
	&__item {
		&:not(:first-child) {
			margin-top: 25px; } }

	&__submit {
		min-height: 50px;
		max-width: 300px;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 30px auto 0;
		@include sm {
			min-height: 80px; } }

	&__submit-button {}

	&__progress-bar {
		pointer-events: none;
		position: absolute;
		height: 10px;
		width: 0;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		border-radius: 200px;
		background: $blue; }

	&__checkmark {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 50%;
		transform: translateY(-50%) translateX(-50%);
		left: 50%;
		right: 0;
		.icon {
			font-size: 40px;
			fill: transparent;
			stroke: $white;
			stroke-width: 2px;
			stroke-dasharray: 42px;
			stroke-dashoffset: 42px;
			stroke-linecap: round;
			stroke-linejoin: round; } } }


.form-input {
	width: 100%; }

.input {
	display: block;
	width: 100%;
	height: 50px;
	padding: 15px 20px;
	border-radius: 4px;
	border: 1px solid rgba($inputBorder, .2);
	color: $gray;
	font-family: $roboto;
	font-size: 14px;
	@include placeholder {
		color: $gray;
		font-size: 14px; }
	&.error {
		color: red;
		border-color: red;
		@include placeholder {
			color: red; } } }

textarea.input {
	height: 157px;
	resize: none; }

label.error {
	position: absolute;
	top: 100%;
	left: 0;
	padding-left: 20px;
	color: red;
	font-size: 12px; }

.message-sending {
	display: none;
	position: absolute;
	z-index: 10;
	top: 0;
	left: 0;
	right: 0;
	bottom: 80px;
	padding-bottom: 80px;
	background-color: $white;
	user-select: none;
	color: $blue;
	@include vertical;
	@include gpu;
	overflow: auto;
	transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
	&__container {
		position: relative;
		z-index: 1;
		font-size: 24px;
		font-weight: 300;
		vertical-align: middle;
		display: inline-block;
		max-width: 430px;
		width: 100%;
		@include sm {
			font-size: 22px; }
		@include xs {
			font-size: 18px; } }
	&__inner {
		max-width: 100%;
		background-color: $white; }
	&__x {
		position: absolute;
		top: 20px;
		right: 40px;
		background-color: transparent;
		font-size: 17px; } }
