[data-tabs] {
    // overflow: hidden
    [data-tabs-control] {
        cursor: pointer;
        user-select: none;
        &[disabled],
        &.is-disabled {
            cursor: not-allowed;
            @include nosel; } }
    [data-tabs-wrapper] {
        position: relative; }
    [data-tabs-container] {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s, visibility 0.3s;
        &.is-open {
            position: relative;
            opacity: 1;
            visibility: visible; } } }

.tabs {
    width: 100%;
    display: flex;
    justify-content: space-between;
    @include sm {
        flex-direction: column; }
    &__out {
        width: 35%;
        @include sm {
            width: 100%; } }
    &__list {
        @include sm {
            display: flex;
            flex-wrap: wrap; } }
    &__wrapper {
        width: 65%;
        @include sm {
            width: 100%;
            margin-top: 20px; } }
    &__container {
        padding-left: 153px;
        @include lg {
            padding-left: 100px; }
        @include md {
            padding-left: 50px; }
        @include sm {
            padding-left: 0; } }
    &__item {
        padding: 30px;
        display: flex;
        align-items: center;
        transition: color .3s, box-shadow .3s;
        border-radius: 7px;
        font-size: 24px;
        color: $gray;
        line-height: 1;
        min-height: 99px;
        text-transform: uppercase;
        &:not(:last-child) {
            margin-bottom: 5px;
            @include sm {
                margin-bottom: 10px; } }
        @include sm {
            margin: 0 10px 10px 0;
            min-height: auto;
            padding: 10px 15px;
            font-size: 13px; }
        &.is-active,&:hover {
            color: $blue;
            box-shadow: 0 0 20px 0 rgba($shadowColor, 0.12);
            svg path {
                fill: $blue; } }
        svg {
            opacity: 1;
            path {
                transition: fill .3s; } } }
    &__icon {
        margin-right: 27px;
        transition: margin .3s;
        @include sm {
            margin-right: 10px; }
        .tabs__item.is-active &,
        .tabs__item:hover & {
            margin-right: 35px;
            background-image: none !important;
            @include sm {
                margin-right: 10px; } }
        .icon {
            display: block;
            // transition: color .3s
            @include sm {
                font-size: 16px; }
            &-html {
                font-size: 19px; }
            &-css,
            &-javascript {
                font-size: 32px; }
            &-animation {
                font-size: 22px; }
            &-resources {
                font-size: 33px; }
            &-github {
                font-size: 36px; } }
        &_pacman {
            background-position: top;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: none !important;
            svg {
                // opacity: 0
                tabs__item.is-active &,
                .tabs__item:hover & {
                    opacity: 1; } } } }
    &__images {
        margin-top: 30px;
        display: flex;
        flex-wrap: wrap; }
    &__img {
        margin: 0 15px 15px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        text-transform: uppercase;
        img {
            display: block;
            max-width: 40px; }
        &_w100 {
            img {
                max-width: 100px; } } }
    &__text {
        max-width: 487px;
        @include sm {
            max-width: none; } } }
